import { Mark } from '@ali/4ever-cangjie';

interface SmallCaps extends Mark {};

const SmallCaps = {
  TYPE: 'smallCaps',

  create(): SmallCaps {
    return Mark.create({ type: SmallCaps.TYPE });
  },

  isSmallCaps(maybeSmallCaps: any): maybeSmallCaps is SmallCaps {
    return Mark.isMark(maybeSmallCaps) && maybeSmallCaps.type === SmallCaps.TYPE;
  },
};

export default SmallCaps;
